<template>
  <div class="container">
    <div class="search">
      <van-row>
        <van-col span="3">
          <van-icon name="arrow-left" class="icon" @click="goHome" />
        </van-col>
        <van-col span="18">
          <van-search v-model="value" placeholder="请输入搜索关键词" shape="round" />
        </van-col>
        <van-col span="3">
          <van-icon name="search" class="icon" @click="search" />
        </van-col>
      </van-row>
    </div>

    <div class="search-history clearfix" v-if="searchList.length">
      <h2>搜索历史</h2>
      <van-button
        type="default"
        v-for="(item,index) in searchList"
        :key="index"
        @click="back(index)"
      >{{item}}</van-button>
    </div>

    <div class="hot-search">
      <h2>热门搜索</h2>

      <van-button
        type="default"
        v-for="(item,index) in productData"
        :key="index"
        @click="getDetalis(item.pid)"
      >{{item.name}}</van-button>
    </div>


    
    
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: "",
      searchList: [],
      productData: [],
      searchData: [],
      searchList: []
    };
  },
  created() {
    let words = ["latte", "coffee", "rena_ice"];
    words.sort((a, b) => {
      return 0.5 - Math.random();
    });
    
    this.getproductData(words[0]);
    this.getproductData(words[1]);
    this.getproductData(words[2]);
    
  },

  methods: {
    goHome() {
      this.$router.push({ name: "推荐" });
    },
    search() {
      if (this.value == "") {
        return;
      } else {
        this.searchList.push(this.value);

        
      }
      
      
      let arr = [];
      for (let i = 0; i < this.searchData.length; i++) {
        if (this.searchData[i].indexOf(this.value) >= 0) {
          arr.push(this.searchData[i]);
        }
        
      }
      //  this.searchList=arr
      //  
      this.value = "";
    },
    back(index) {
      
      this.value = this.searchList[index];
    },
    getDetalis(pid) {
      
      this.$router.push({
        path: "/detalis",
        query: {
          pid
        }
      });
    },
    getproductData(value) {
      this.axios({
        method: "get",
        url: "/typeProducts",
        params: {
          appkey: this.appkey,
          key: "type",
          value: value
        }
      })
        .then(res => {
          
          let data = res.data.result;
          this.productData = res.data.result;
          data.forEach(v => {
            this.searchData.push(v.name);
          });
          //  this.searchData.push(...data)
        })
        .catch(err => {
          
        });
    }
  }
};
</script>

<style lang="less" scoped>
.container {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  .search {
    .van-row {
      text-align: center;
    }
    .icon {
      display: block;
      font-size: 25px;
      margin-top: 14px;
    }
  }
  .search-history {
    width: 80%;
    margin: 0 auto;
    h2 {
      margin: 0;
      padding: 0;
    }
  }
  .hot-search {
    width: 80%;
    margin: 0 auto;
  }
}
.van-button {
  margin: 10px 5px;
}
</style>


